.specProjectsPreview {
  width: 100%;
  height: 100%;
  z-index: 999;
  background-image: url("../../../images/specProjectPreviewBackground.png");
  background-color: #282C3B;
  background-size: cover;
  border-top: 4px solid transparent;
  border-image: linear-gradient(90deg, #FA6E03 , #FAB803);
  border-image-slice: 1;

  .info {
    @media screen and (min-width: 1440px) {
      max-width: 1272px;
      margin: 0 auto;
    }

    @media screen and (min-width: 320px) and (max-width: 1439px) {
      margin: 0 18px;
    }

    &__heading {
      font-size: 36px;
      font-family: "PT Root UI Bold";
      color: white;
      margin-top: 50px;
      margin-bottom: 21px;

      @media screen and (min-width: 320px) and (max-width: 1439px) {
        margin: 28px 0 22px 0;
      }
    }

    &__list {
      @media screen and (min-width: 768px) {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: auto;
        grid-column-gap: 24px;
        padding-bottom: 60px;
      }
    }

    .list {
      &__item {
        @media screen and (min-width: 320px) and (max-width: 767px) {
          .fullFeedCard {
            background-color: transparent;
            .content {
              &__heading {
                color: #FFFFFF !important;
              }
            }
          }
        }
      }
    }
  }

  .menu {
    margin-bottom: 24px;
    .item {
      width: calc(50% + 24px);
      &:last-of-type {
        margin-right: 0;
      }
      &__switcherHeading {
        position: relative;
        margin: 0px 0px 12px;
        font-size: 24px;
        font-family: "PT Root UI Medium";
        font-weight: 400;
        padding-bottom: 12px;

        @media screen and (min-width: 320px) and (max-width: 767px) {
          font-size: 18px;
        }
      }
    }
  }
}