          grid-row-end: 11;
        }

        &:nth-of-type(4) {
          grid-column-start: 1;
          grid-column-end: 2;
          grid-row-start: 14;
          grid-row-end: 15;
        }

        &:nth-of-type(5) {
          grid-column-start: 1;
          grid-column-end: 2;
          grid-row-start: 18;
          grid-row-end: 19;
        }

        &:nth-of-type(6) {
          grid-column-start: 1;
          grid-column-end: 2;
          grid-row-start: 23;
          grid-row-end: 24;
        }

        &:nth-of-type(7) {
          grid-column-start: 1;
          grid-column-end: 2;
          grid-row-start: 28;
          grid-row-end: 29;
        }

        &:nth-of-type(8) {
          grid-column-start: 1;
          grid-column-end: 2;
          grid-row-start: 33;
          grid-row-end: 34;
        }
      }
    }

    @media screen and (min-width: 768px) and (max-width: 1439px) {
      display: grid;
      grid-template-rows: minmax(100px, auto);
      grid-template-columns: repeat(2, calc(50% - 24px));
      grid-gap: 24px;

      margin-bottom: 45px;

      .showMoreButton {
        height: 60px;
        grid-column-start: 1;
