          grid-row-start: 1;
          grid-row-end: 2;
        }

        &:nth-of-type(2) {
          grid-column-start: 1;
          grid-column-end: 2;
          grid-row-start: 6;
          grid-row-end: 7;
        }

        &:nth-of-type(3) {
          grid-column-start: 1;
          grid-column-end: 2;
