          grid-row-end: 34;
        }
      }
    }

    @media screen and (min-width: 1440px) {
      display: grid;
      grid-template-rows: minmax(300px, auto) minmax(300px, auto) 200px minmax(300px, auto) minmax(300px, auto) 72px 200px;
      grid-row-gap: 24px;
      grid-column-gap: 24px;
      grid-template-columns: repeat(4, 300px);

      margin-bottom: 45px;

      .showMoreButton {
        grid-column-start: 1;
        grid-column-end: 5;
        grid-row-start: 6;
        grid-row-end: 7;
      }

      .advertisement {
        &:nth-of-type(1) {
          grid-column-start: 1;
          grid-column-end: 2;
          grid-row-start: 3;
          grid-row-end: 4;
        }

        &:nth-of-type(2) {
          grid-column-start: 2;
          grid-column-end: 3;
          grid-row-start: 3;
          grid-row-end: 4;
        }

        &:nth-of-type(3) {
          grid-column-start: 3;
          grid-column-end: 5;
          grid-row-start: 3;
          grid-row-end: 4;
        }

        &:nth-of-type(4) {
          grid-column-start: 1;
          grid-column-end: 3;
          grid-row-start: 7;
          grid-row-end: 8;
        }

        &:nth-of-type(5) {
          grid-column-start: 3;
          grid-column-end: 4;
          grid-row-start: 7;
          grid-row-end: 8;
        }

        &:nth-of-type(6) {
          grid-column-start: 4;
          grid-column-end: 5;
          grid-row-start: 7;
          grid-row-end: 8;
        }
      }
    }
  }
}