.menuUnderlineSwitcher {
  height: 100%;
  margin-bottom: 8px;
  display: flex;
  align-items: center;
  position: relative;

  &__line {
    position: absolute;
    height: 2px;
    background-color: #FA6E03;
    border: 0px;
    bottom: -2px;
    margin: 0;
    transition: .3s ease-in-out;
  }

  &__item {
    cursor: pointer;
    font-size: 24px;
    font-family: "PT Root UI Medium";
    color: white;
    margin-right: 25px;

    &_active{
      color: #FA6E03 !important;
    }
  }
}