.separateComment {
  display: flex;
  flex-direction: column;

  margin-bottom: 22px;

  &__author {
    margin-bottom: 12px;
  }

  .author {
    display: flex;
    align-items: center;
    &__name {
      margin: 0 6px 0 0;
      font-family: "PT Root UI Bold";
      font-size: 16px;
    }

    &__date {
      font-family: "PT Root UI Regular";
      font-size: 13px;
    }
  }

  &__text {
    margin-bottom: 8px;
    font-family: "PT Root UI Regular";
    font-size: 16px;
  }

  .buttons {
    &__item {
      font-family: "PT Root UI Regular";
      font-size: 13px;

      &:not(:last-of-type) {
        margin-right: 16px;
      }
    }
  }
}