.share {
  display: flex;
  align-items: center;
  overflow: hidden;
  padding-top: 16px;

  @media screen and (min-width: 320px) and (max-width: 767px) {
    flex-direction: column;
    align-items: flex-start;
    overflow: hidden;

    &__text {
      margin-bottom: 8px;
    }

    &__cards {
      width: 100%;
      overflow: scroll;
      & > .shareCard:not(:last-of-type) {
        margin-left: 0;
        margin-right: 12px;
      }
    }
  }

  &_border_top {
    border-top: 1px solid #E0E1E4;
  }

  &__text {
    margin-right: 10px;
    color: #626677;
    font-size: 18px;
    font-family: "PT Root UI Regular";
  }

  &__cards {
    display: flex;
    flex-grow: 1;

    &_aside {
      .shareCard:last-of-type {
        margin-left: auto;
      }
    }

    & > .shareCard  {
      margin-left: 8px;
    }
  }
}