.photoPage {
  flex-grow: 1;
  background-image: url("../../../../images/specProjectPreviewBackground.png");
  background-color: #282C3B;
  background-repeat: repeat-x;
  color: white;
  padding-top: 48px;
  padding-bottom: 48px;
  overflow: hidden;

  @media screen and (min-width: 320px) and (max-width: 767px) {
    padding-top: 24px;
  }

  @media screen and (min-width: 768px) and (max-width: 1439px) {
    padding-top: 36px;
  }

  & > .content {
    @media screen and (min-width: 1440px) {
      width: 1272px;
      margin: 0 auto 30px;
    }

    @media screen and (min-width: 320px) and (max-width: 767px) {
      margin: 0 18px;
    }

    @media screen and (min-width: 768px) and (max-width: 1439px) {
      margin: 0 36px;
    }

    .content {
      &__photosHeading {
        margin: 0 0 30px;
        font-size: 36px;
        font-family: "PT Root UI Bold";

        @media screen and (min-width: 320px) and (max-width: 767px) {
          font-size: 24px;
        }

        @media screen and (min-width: 768px) and (max-width: 1439px) {
          font-size: 28px;
        }
      }
    }

    .recommendedFeed {
      & > .recommended:after {
        background-color: #282C3B;
      }
    }

    .photosList {

      @media screen and (min-width: 320px) and (max-width: 767px) {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: minmax(200px, auto);
        grid-gap: 24px;
      }

      @media screen and (min-width: 768px) and (max-width: 1439px) {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: minmax(200px, auto);
        grid-gap: 12px;
      }

      @media screen and (min-width: 1440px) {
        display: grid;
        grid-template-columns: 312px 300px 312px 312px;
        grid-template-rows: repeat(6, 208px);
        grid-gap: 12px;
        margin-bottom: 24px;
      }

      &__image {
        border-radius: 8px;

        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;

        img {
          flex-shrink: 0;
          min-width: 100%;
          min-height: 100%
        }

        @media screen and (min-width: 768px) and (max-width: 1439px) {
          &:nth-child(1) {
            grid-column-start: 1;
            grid-column-end: 3;
            grid-row-start: 1;
            grid-row-end: 2;
          }
        }

        @media screen and (min-width: 1440px) {
          &:nth-child(1) {
            grid-column-start: 1;
            grid-column-end: 2;
            grid-row-start: 1;
            grid-row-end: 2;
          }
          &:nth-child(2) {
            grid-column-start: 2;
            grid-column-end: 4;
            grid-row-start: 1;
            grid-row-end: 3;
          }
          &:nth-child(3) {
            grid-column-start: 4;
            grid-column-end: 5;
            grid-row-start: 1;
            grid-row-end: 2;
          }
          &:nth-child(4) {
            grid-column-start: 1;
            grid-column-end: 2;
            grid-row-start: 2;
            grid-row-end: 3;
          }
          &:nth-child(5) {
            grid-column-start: 4;
            grid-column-end: 5;
            grid-row-start: 2;
            grid-row-end: 3;
          }
          &:nth-child(6) {
            grid-column-start: 1;
            grid-column-end: 3;
            grid-row-start: 3;
            grid-row-end: 5;
          }
          &:nth-child(7) {
            grid-column-start: 3;
            grid-column-end: 4;
            grid-row-start: 3;
            grid-row-end: 4;
          }
          &:nth-child(8) {
            grid-column-start: 3;
            grid-column-end: 4;
            grid-row-start: 4;
            grid-row-end: 5;
          }
          &:nth-child(9) {
            grid-column-start: 4;
            grid-column-end: 5;
            grid-row-start: 3;
            grid-row-end: 4;
          }
          &:nth-child(10) {
            grid-column-start: 4;
            grid-column-end: 5;
            grid-row-start: 4;
            grid-row-end: 5;
          }
          &:nth-child(11) {
            grid-column-start: 1;
            grid-column-end: 2;
            grid-row-start: 5;
            grid-row-end: 6;
          }
          &:nth-child(12) {
            grid-column-start: 2;
            grid-column-end: 4;
            grid-row-start: 5;
            grid-row-end: 7;
          }
          &:nth-child(13) {
            grid-column-start: 4;
            grid-column-end: 5;
            grid-row-start: 4;
            grid-row-end: 5;
          }
        }
      }
    }

    .shareSection {
      display: flex;
      justify-content: center;
      padding-top: 8px;
      border-top: 1px solid #3B3F4D;
      margin-bottom: 45px;
    }
  }
}