.specProjects {
  display: flex;
  justify-content: center;
  flex-grow: 1;
  background-image: url("../../../../images/specProjectPreviewBackground.png");
  background-color: #282C3B;
  background-repeat: repeat-x;
  color: white;
  padding-top: 72px;

  @media screen and (min-width: 320px) and (max-width: 767px) {
    justify-content: center;
  }

  @media screen and (min-width: 768px) and (max-width: 1439px) {
    justify-content: center;
  }

  &__content {
    @media screen and (min-width: 1440px) {
      max-width: 1272px;
    }

    @media screen and (min-width: 320px) and (max-width: 767px) {
      margin: 0 20px;
    }

    @media screen and (min-width: 768px) and (max-width: 1439px) {
      margin: 0 20px;
    }
  }

  & > .content {
    display: flex;
    justify-content: space-between;

    @media screen and (min-width: 320px) and (max-width: 1439px) {
      flex-direction: column-reverse;
      .content__column {
        & > * {
          margin-bottom: 24px;
        }
      }
    }

    @media screen and (min-width: 1440px) {
      .content__column {
        width: calc(50% - 12px);
        & > * {
          margin-bottom: 24px;
        }
      }
    }

    .column__heading {
      min-height: 100px;
      font-size: 36px;
      font-family: "PT Root UI Bold";
      text-align: right;

      @media screen and (min-width: 320px) and (max-width: 767px) {

      }

      @media screen and (min-width: 768px) and (max-width: 1439px) {
        font-size: 40px;
        min-height: 40px;
      }
    }
  }
}