.mediaPreview {
  width: 100%;
  z-index: 999;
  background-image: url("../../../images/specProjectPreviewBackground.png");
  background-color: #282C3B;
  background-size: cover;
  border-top: 4px solid transparent;
  border-image: linear-gradient(90deg, #FA6E03, #FAB803);
  border-image-slice: 1;

  padding-top: 50px;
  padding-bottom: 60px;

  .info {
    @media screen and (min-width: 1440px) {
      max-width: 1272px;
      margin: 0 auto;
    }

    &__heading {
      font-size: 36px;
      font-family: "PT Root UI Bold";
      color: white;
      margin: 0 0 21px;

      @media screen and (min-width: 320px) and (max-width: 1439px) {
        margin-left: 26px;
        margin-right: 26px;
      }

      &__heading {
        position: relative;
        color: #FFFFFF;
        margin: 0px;
        margin-bottom: 12px;
        font-size: 24px;
        font-family: "PT Root UI Medium";
        font-weight: 400;
        padding-bottom: 12px;
        margin-bottom: 24px;

        &_orange {
          color: #FA6E03;
          border-bottom: 2px solid #FA6E03;
        }

        &_white {
          color: #FFFFFF;
          border-bottom: 2px solid #FFFFFF;

          &:after {
            content: '';
            height: 2px;
            width: 50%;
            background-color: #FFFFFF;
            position: absolute;
            bottom: -2px;
            left: -24px;
          }
        }
      }
    }

    .content {
      .mediaList {
        .navigation {
          &__item {
            display: flex;
            align-items: center;

            &_type {
              &_withSvg {
                color: #FA6E03;
                font-size: 16px;
                font-family: "PT Root UI Medium";

                svg {
                  margin-left: 12px;
                }
              }
            }
          }
        }

        @media screen and (min-width: 320px) and (max-width: 767px) {
          display: grid;
          justify-content: center;
          grid-template-rows: 40px 350px;
          grid-template-columns: 100%;
          grid-gap: 24px;
          margin: 0 24px;

          .advertisement {
            display: none;
          }

          .photoFeedCard {
            .content {

            }
          }
        }

        @media screen and (min-width: 768px) and (max-width: 1439px) {
          display: grid;
          justify-content: center;
          grid-template-rows: 40px 350px;
          grid-template-columns: repeat(2, calc(50% - 24px));
          grid-gap: 24px;
          margin: 0 18px;

          .navigation {
            grid-row-start: 1;
            grid-row-end: 2;
            grid-column-start: 1;
            grid-column-end: 3;
          }

          .advertisement {
            display: none;
          }
        }

        @media screen and (min-width: 1440px) {
          display: grid;
          grid-row-gap: 16px;
          grid-column-gap: 24px;
          grid-template-rows: 50px 200px 200px;
          grid-template-columns: 624px 300px 300px;
          grid-template-areas:  "nav1 nav1 ."
                              "photoCard1 photoCard2 ad1"
                              "photoCard1 photoCard3 ad1";

          .navigation {
            position: relative;
            height: 100%;

            &:nth-of-type(1) {
              grid-area: nav1;
            }
          }

          .advertisement {
            &:nth-of-type(2) {
              grid-area: ad1;
            }
          }

          .photoFeedCard {
            &:nth-of-type(1) {
              grid-area: photoCard1;
            }

            &:nth-of-type(2) {
              grid-area: photoCard2;
            }

            &:nth-of-type(3) {
              grid-area: photoCard3;
            }
          }
        }
      }
    }
  }
}