.comments {
  &__amount{
    margin: 0px 0px 8px;
  }

  .input {
    background-color: #FFFFFF;
    border-radius: 8px;
    padding: 4px 4px 4px 12px;
    display: flex;
    margin-bottom: 22px;

    &__area {
      font-size: 16px;
      font-family: "PT Root UI Regular";
      color: #626677;
      flex-grow: 1;
      border: 0px;
      outline: none;
    }

    &__button {
      width: 40px;
      height: 40px;
      border-radius: 8px;
      background-color: #FA6E03;
      border: 0px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  &__menuHeader {
    display: flex;
    align-items: center;

    svg {
      margin-right: 8px;
    }
  }

  &__list {
    padding-left: 24px;

    & > .showMoreButton {
      background-color: #E0E1E4;
      height: 48px;

      .showMoreButton__text {
        color: #626677;
        font-size: 18px;
        font-family: "PT Root UI Regular";
      }
    }
  }
}