.feedPreview {
  position: relative;
  min-height: 460px;
  background-image: url("../../../images/bigBackground.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;

  @media screen and (min-width: 768px) {
    padding: 0 84px;

  }

  &__card {
    @media screen and (min-width: 1440px) {
      max-width: 1272px;
      margin: 0 auto;
    }

    .fullFeedCard {
      position: absolute;
      background: rgba(0, 0, 0, 0.5);

      @media screen and (min-width: 768px) {
        width: 476px;
        height: 340px;
        top: 60px;

        .content {
          &__heading {
            font-size: 30px;
          }
        }
      }

      @media screen and (min-width: 320px) and (max-width: 767px) {
        width: auto;
        height: auto;
        margin: 0 18px;
        bottom: 30px;

        .content {
          margin: 16px 16px 8px 16px;
          &__heading {
            font-size: 20px;
            color: #FFFFFF;
          }

          .feedInfo * {
            color: white;
            fill: #FFFFFF;
          }
        }
      }
    }
  }
}