.smallArticle {
  margin: 0 auto;
  & > .mainPart {
    display: flex;
    justify-content: space-between;
    @media screen and (min-width: 1440px) {
      width: 1272px;
      margin: 36px auto 0;

      .category {
        margin-left: 16px;
      }

      .article {
        & > section:not(:last-of-type) {
          margin-bottom: 36px;
        }

        margin-right: 108px;
        &_centered {
          margin-left: 108px;
        }

        &_cardAside {
          display: flex;
          margin-bottom: 36px;
          .feedCard {
            width: 300px;
            flex: 1 0 auto;
            margin-right: 24px;
          }
        }
      }

      .aside {
        width: 300px;
        flex-shrink: 0;
        flex-grow: 0;

        display: grid;
        grid-template-rows: 200px 200px 432px 656px 200px 200px 432px;
        grid-template-columns: 300px;
        grid-row-gap: 24px;
      }

      .mainContent {
        flex-grow: 0;
        margin-right: 24px;
        &__heading {
          font-size: 36px;
          margin: 8px 0px;
        }
      }
    }

    .tags {
      display: flex;
      .tag {
        margin-right: 4px;
      }
    }

    .text {
      color: #282C3B;

      &__header {
        font-size: 24px;
        font-family: "PT Root UI Medium";
      }

      &__subtext {
        font-size: 20px;
        font-family: "PT Root UI Regular"
      }

      &__sliderCard {
        height: 412px;
      }
    }

    .mainContent {
      .feedInfo {
        justify-content: space-between;
        color: #9B9FAD;

        svg path {
          fill: #282C3B;
        }
      }

      & > section {
        margin-bottom: 48px;
      }

      .ads {
        display: flex;

        & > .advertisement {
          &:first-of-type {
            flex-grow: 70;
            margin-right: 24px;
          }
          &:last-of-type {
            flex-grow: 22;
          }
        }
      }

      &__heading {
        font-family: "PT Root UI Bold";
      }

      &__stats {
        margin-bottom: 24px;
      }

      &__mainImage {
        margin-bottom: 14px;
      }

      &__comments {
        & > .comments {
          & > .menuUnderlineSwitcher {
            .menuUnderlineSwitcher__item {
              font-size: 16px;
              font-family: "PT Root UI Regular";
              margin-bottom: 12px;
            }
          }
        }
      }
    }
  }

  &__recommendedArticles {
    margin-bottom: 70px;
  }

  & > .ads {
    display: grid;
    grid-template-columns: 300px 300px 624px;
    grid-template-rows: 200px;
    grid-column-gap: 24px;

    margin-bottom: 45px;
  }
}