.bigArticle {
  flex-grow: 1;

  @media screen and (min-width: 1440px) {
    & > section:not(:first-of-type) {
      max-width: 1272px;
      margin-left: auto;
      margin-right: auto;
    }

    & > section:first-of-type {
      width: 100%;
    }
  }

  & > .header {
    position: relative;
    .header {
      &__contentWrapper {
        max-width: 1056px;
        margin: 0 auto;
        & > .value {
          color: white;
          max-width: 1056px;
          position: absolute;
          bottom: 40px;
          z-index: 1;

          .value__heading {
            font-size: 36px;
            font-family: "PT Root UI Bold";
          }

          .tags {
            display: flex;

            & > .tag {
              margin-right: 4px;
              background-color: rgba(255, 255, 255, 0.2);

              .tag__name {
                color: #FFFFFF;
              }
            }
          }

          .feedInfo {
            justify-content: space-between;
            margin-bottom: 22px;

            svg path {
              fill: #FFFFFF;
            }
          }
        }
      }

      &__mainImage {
        img {
          width: 100%;
          max-height: 810px;
          object-fit: cover;
        }

        &:after {
          content: '';
          position: absolute;
          top: 0;
          right: 0;
          width: 100%;
          height: 100%;
          background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 1));
        }
      }
    }
  }

  & > .mainPart {
    display: flex;
    justify-content: space-between;
    @media screen and (min-width: 1440px) {
      margin: 36px auto 0;

      .category {
        margin-left: 16px;
      }

      .article {
        & > section:not(:last-of-type) {
          margin-bottom: 36px;
        }

        margin-right: 108px;
        &_centered {
          margin-left: 108px;
        }

        &_cardAside {
          display: flex;
          margin-bottom: 36px;
          .feedCard {
            width: 300px;
            flex: 1 0 auto;
            margin-right: 24px;
          }
        }
      }

      .aside {
        width: 300px;
        flex-shrink: 0;
        flex-grow: 0;

        display: grid;
        grid-template-rows: 200px 200px 432px 656px 200px 200px 432px;
        grid-template-columns: 300px;
        grid-row-gap: 24px;
      }

      .mainContent {
        flex-grow: 0;
        margin-right: 24px;
        &__heading {
          font-size: 36px;
          margin: 8px 0px;
        }
      }
    }

    .tags {
      display: flex;
      .tag {
        margin-right: 4px;
      }
    }

    .text {
      color: #282C3B;

      &__header {
        font-size: 24px;
        font-family: "PT Root UI Medium";
      }

      &__subtext {
        font-size: 20px;
        font-family: "PT Root UI Regular"
      }

      &__sliderCard {
        height: 412px;
      }
    }

    .mainContent {
      .feedInfo {
        justify-content: space-between;
        color: #9B9FAD;

        svg path {
          fill: #282C3B;
        }
      }

      & > section {
        margin-bottom: 48px;
      }

      .ads {
        display: flex;

        & > .advertisement {
          &:first-of-type {
            flex-grow: 70;
            margin-right: 24px;
          }
          &:last-of-type {
            flex-grow: 22;
          }
        }
      }

      &__heading {
        font-family: "PT Root UI Bold";
      }

      &__stats {
        margin-bottom: 24px;
      }

      &__mainImage {
        margin-bottom: 14px;
      }

      &__comments {
        & > .comments {
          & > .menuUnderlineSwitcher {
            .menuUnderlineSwitcher__item {
              font-size: 16px;
              font-family: "PT Root UI Regular";
              margin-bottom: 12px;
            }
          }
        }
      }
    }
  }

  &__recommendedArticles {
    margin-bottom: 70px;
  }

  & > .ads {
    display: grid;
    grid-template-columns: 300px 300px 624px;
    grid-template-rows: 200px;
    grid-column-gap: 24px;

    margin-bottom: 45px;
  }
}