.allTags {
  display: flex;
  font-size: 14px;
  font-family: "PT Root UI Regular";

  & > .tag {
    background-color: rgba(255, 255, 255, 0.2);

    .tag__name {
      color: #FFFFFF;
    }

    &:not(:first-of-type){
      margin-left: 4px;
    }

    &:last-of-type {
      margin-left: 12px;
    }
  }
}